#veg-only-chkbox[type="checkbox"] {
  appearance: none;
  cursor: pointer;
  background: #ccc;
  border: 3px solid #ccc;
  border-radius: 6px;
  width: 40px;
  height: 20px;
  transition: all .4s;
  position: relative;
}

